/* header css above */
.transparent-head{
  background-color: rgba(0,0,0,0.95) !important;
  
}

.transparent-head .nav-link{
  font-size: 21px;
}

.navbar-nav > li{
  padding-left:30px;
  padding-right:30px;
}

.navbar-nav a:hover{
  color: green !important;
  font-size: 27px;
}

.navbar-nav>.active>a {
  color: green !important;
  font-size: 27px;
}
